const REQUEST_CHANGE_SEED_TAB = 'REQUEST_CHANGE_SEED_TAB'
const RESPONSE_CHANGE_SEED_TAB = 'RESPONSE_CHANGE_SEED_TAB'

const requestChangeSeedTabAction = () => {
  return {
    type: REQUEST_CHANGE_SEED_TAB
  }
}

const responseChangeSeedTabAction = (seed) => {
  return {
    type: RESPONSE_CHANGE_SEED_TAB,
    seed
  }
}

const changeSeedTab = (seed) => {
  return (dispatch) => {
    dispatch(requestChangeSeedTabAction());
    dispatch(responseChangeSeedTabAction(seed))
  }
}

export {
  requestChangeSeedTabAction,
  responseChangeSeedTabAction,
  changeSeedTab,
  REQUEST_CHANGE_SEED_TAB,
  RESPONSE_CHANGE_SEED_TAB,
}
