import React from 'react';
import MainButton from './MainButton'

import { withActiveStep } from '../containers/containerActiveStep'
import { withSteps } from '../containers/containerSteps'
import { withActiveScreen } from '../containers/containerActiveScreen'

import styles from './stepsControls.module.scss'

const StepsControls = function (props) {
  const { activeStep, onResetActiveStep, steps, onGetScreen, onSetStep } = props

  const handleChangeStepNext = event => {
    let stepIndex = steps.stepsList.findIndex(item => item.id === activeStep.step.id);

    if (stepIndex < steps.stepsList.length - 1) {
      onSetStep(steps.stepsList[stepIndex + 1])
    }
  }

  const handleChangeStepPrev = event => {
    let stepIndex = steps.stepsList.findIndex(item => item.id === activeStep.step.id);

    if (stepIndex === 0) {
      onGetScreen('start')
      onResetActiveStep()
    } else {
      onSetStep(steps.stepsList[stepIndex - 1])
    }
  }

  return (
    <div className={styles.stepsControls}>
      <div className={styles.button_container}>
        <MainButton
          color="text"
          type="prev"
          label="Previous"
          click={handleChangeStepPrev}
        />
      </div>
      <div className={styles.button_container}>
        <MainButton
          color="primary"
          type="next"
          label="Next Step"
          click={handleChangeStepNext}
        />
      </div>
    </div>
  )
}

export default React.memo(withActiveScreen(withSteps(withActiveStep(StepsControls))))
