const REQUEST_OPEN_SCREEN = 'REQUEST_OPEN_SCREEN'
const RESPONSE_OPEN_SCREEN = 'RESPONSE_OPEN_SCREEN'

const requestActiveScreen = () => {
  return {
    type: REQUEST_OPEN_SCREEN
  }
}

const responseActiveScreen = (screen) => {
  return {
    type: RESPONSE_OPEN_SCREEN,
    screen
  }
}

const getScreen = (screen) => {
  return (dispatch) => {
    dispatch(requestActiveScreen())
    dispatch(responseActiveScreen(screen))
  }
}

export {
  requestActiveScreen,
  responseActiveScreen,
  getScreen,
  REQUEST_OPEN_SCREEN,
  RESPONSE_OPEN_SCREEN,
}
