import {
  REQUEST_SET_STEP,
  RESPONSE_SET_STEP,
  REQUEST_RESET_ACTIVE_STEP,
  RESPONSE_RESET_ACTIVE_STEP
} from '../actions/activeStepActions'

import { states } from './queriesStates'

const initialState = {
  dataState: states.NOT_REQUESTED,
  step: null
}

const activeStepReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_SET_STEP: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.REQUEST
        }
      )
    }
    case RESPONSE_SET_STEP: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.READY,
          step: action.step
        }
      )
    }
    case REQUEST_RESET_ACTIVE_STEP: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.REQUEST
        }
      )
    }
    case RESPONSE_RESET_ACTIVE_STEP: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.NOT_REQUESTED,
          step: null
        }
      )
    }
    default:
      return state
  }
}

export default activeStepReducer