const REQUEST_SET_STEP = 'REQUEST_SET_STEP'
const RESPONSE_SET_STEP = 'RESPONSE_SET_STEP'
const REQUEST_RESET_ACTIVE_STEP = 'REQUEST_RESET_ACTIVE_STEP'
const RESPONSE_RESET_ACTIVE_STEP = 'RESPONSE_RESET_ACTIVE_STEP'

const requestSetStep = () => {
  return {
    type: REQUEST_SET_STEP
  }
}

const responseSetStep = (step) => {
  return {
    type: RESPONSE_SET_STEP,
    step
  }
}

const requestResetActiveStep = () => {
  return {
    type: REQUEST_RESET_ACTIVE_STEP
  }
}

const responseResetActiveStep = () => {
  return {
    type: RESPONSE_RESET_ACTIVE_STEP
  }
}

const setStep = (step) => {
  return (dispatch) => {
    dispatch(requestSetStep())
    dispatch(responseSetStep(step))
  }
}

const resetActiveStep = () => {
  return (dispatch) => {
    dispatch(requestResetActiveStep())
    dispatch(responseResetActiveStep())
  }
}

export {
  requestSetStep,
  responseSetStep,
  setStep,
  requestResetActiveStep,
  responseResetActiveStep,
  resetActiveStep,
  REQUEST_SET_STEP,
  RESPONSE_SET_STEP,
  REQUEST_RESET_ACTIVE_STEP,
  RESPONSE_RESET_ACTIVE_STEP
}
