import { connect } from 'react-redux'
import get from 'lodash/get'

import {
  getStepsList as onGetStepsList
} from '../actions/stepsActions'

const mapStateToProps = (state) => ({
  steps: {
    dataState: state.steps.dataState,
    stepsList: state.steps.stepsList
  }
})

const mapDispatchToProps = {
  onGetStepsList
}

const withSteps = Component => connect(mapStateToProps, mapDispatchToProps)(Component)

export { withSteps }