import React from 'react';

import styles from './title.module.scss'

const Title = function (props) {
  const { title, extra, type } = props

  return (
    type === 'main'
      ? <h1 className={extra !== undefined ? styles.mainTitle_extra : styles.mainTitle}>{title}</h1>
      : type === 'secondary'
        ? <h2 className={extra !== undefined ? styles.secondaryTitle_extra : styles.secondaryTitle}>{title}</ h2>
        : null
  )
}

export default React.memo(Title)
