import React, { Component } from 'react';
import { animateScroll as scroll } from 'react-scroll'
import { CSSTransition } from 'react-transition-group'
import StartScreen from './screens/StartScreen'
import StepScreen from './screens/StepScreen'

import { withActiveScreen } from '../containers/containerActiveScreen'
import { withActiveStep } from '../containers/containerActiveStep'
import { states } from '../reducers/queriesStates'

import styles from './app.module.scss'
import fadeTransition from "../transitions/fade.module.scss"

class App extends Component {
  componentDidMount() {
    const { onGetScreen, activeScreen } = this.props

    if (activeScreen.dataState === states.NOT_REQUESTED)
      onGetScreen('start')
  }

  componentDidUpdate(prevProps) {
    const { activeScreen, activeStep } = this.props

    if (activeScreen.screen !== prevProps.activeScreen.screen)
      window.scrollTo(0, 0)

    if (activeStep.dataState === "STATE_READY" && prevProps.activeStep.step !== activeStep.step)
      window.scrollTo(0, 0)

  }

  render() {
    const { activeScreen } = this.props

    return (
      <div className="bodyInner">
        <CSSTransition
          timeout={500}
          in={activeScreen.screen === 'start'}
          classNames={fadeTransition}
          unmountOnExit
        >
          <div className={styles.screen_container}>
            <StartScreen />
          </div>
        </CSSTransition>

        <CSSTransition
          timeout={500}
          in={activeScreen.screen === 'step'}
          classNames={fadeTransition}
          unmountOnExit
        >
          <div className={styles.screen_container}>
            <StepScreen />
          </div>
        </CSSTransition>
      </div>
    )
  }
}

export default withActiveScreen(withActiveStep(App));
