import React from 'react';
import { ThemeProvider } from "@material-ui/styles"
import Slider from '@material-ui/core/Slider'
import FormControl from '@material-ui/core/FormControl'
import TextField from '@material-ui/core/TextField'
// import MainIcon from '../../../shared/MainIcon'

import { muiCustomTheme } from './static.MuiCustomTheme'
import styles from './slider.module.scss'

function ThumbCustom(props) {
  return (
    <span {...props}>
      <span className={styles.thumbShadow} />
    </span>
  );
}

const CustomSlider = function (props) {
  const { placeholder, id, value } = props

  return (
    <ThemeProvider theme={muiCustomTheme}>
      <Slider
        ThumbComponent={ThumbCustom}
      />
    </ThemeProvider>
  )
}

export default React.memo(CustomSlider)