import {
  REQUEST_STEPS_LIST,
  RESPONSE_STEPS_LIST,
} from '../actions/stepsActions'

import { states } from './queriesStates'

const initialState = {
  dataState: states.NOT_REQUESTED,
  stepsList: null
}

const stepsReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_STEPS_LIST: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.REQUEST
        }
      )
    }
    case RESPONSE_STEPS_LIST: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.READY,
          stepsList: action.stepsList
        }
      )
    }
    default:
      return state
  }
}

export default stepsReducer