import { connect } from 'react-redux'
import get from 'lodash/get'

import {
  changeRequestsCount as onChangeRequestsCount
} from '../actions/requestsCounterActions'

const mapStateToProps = (state) => ({
  requestsCounterValue: {
    dataState: state.requestsCounterValue.dataState,
    activeRequestValue: state.requestsCounterValue.activeRequestValue
  }
})

const mapDispatchToProps = {
  onChangeRequestsCount
}

const withRequestsCounter = Component => connect(mapStateToProps, mapDispatchToProps)(Component)

export { withRequestsCounter }