import {
  REQUEST_CHANGE_SEED_TAB,
  RESPONSE_CHANGE_SEED_TAB,
} from '../actions/secretSeedTabsActions'
import { states } from './queriesStates'

const initialState = {
  dataState: states.NOT_REQUESTED,
  activeSeed: null
}

const secretSeedTabReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_CHANGE_SEED_TAB: {
      return Object.assign(
        {},
        state,
        {
          dataState: 'STATE_REQUEST',
        }
      )
    }
    case RESPONSE_CHANGE_SEED_TAB: {
      return Object.assign(
        {},
        state,
        {
          dataState: 'STATE_READY',
          activeSeed: action.seed
        }
      )
    }
    default:
      return state
  }
}

export default secretSeedTabReducer