import React from 'react';
import Title from '../../shared/Title'
import NetworkValue from '../steps/controls/NetworkValue'
import NetworkDnsValue from '../steps/controls/NetworkDnsValue'
import StepsControls from '../../shared/StepsControls'

import styles from './stepContent.module.scss'

const NetworkOptionsWrapper = function () {
    return (
        <div className={styles.stepContent}>
            <div className={styles.secondTitle_container}>
                <Title type="secondary" title="Network Options" />
            </div>

            <div className={styles.stepContentItem_container}>
                <NetworkValue />
            </div>

            <div className={styles.stepContentItem_container}>
                <NetworkDnsValue />
            </div>

            <div className={styles.stepsControls_container}>
                <StepsControls />
            </div>
        </div>
    )
}

export default React.memo(NetworkOptionsWrapper)