import React from 'react';
import MainIcon from '../../../shared/MainIcon'

import styles from './secretSeedTabItem.module.scss'

const SecretSeedTabItem = function (props) {
  const { icon, caption, active, handleChange } = props

  return (
    <div
      className={!!active ? styles.secretSeedTabItem_active : styles.secretSeedTabItem}
      onClick={handleChange}
    >
      <div className={styles.icon_container}>
        <MainIcon name={icon} />
      </div>

      <span className={styles.caption}>{caption}</span>
    </div>
  )
}

export default React.memo(SecretSeedTabItem)
