import React, { Component } from 'react';
import SecretSeedTabItem from './SecretSeedTabItem';

import { withSecretSeedTabs } from '../../../containers/containerSecretSeedTabs';
import SecretSeedTypes from '../../static/static.secretSeeds'
import { states } from '../../../reducers/queriesStates'

import styles from './secretSeedTabs.module.scss'

class SecretSeedTabs extends Component {
  componentDidMount() {
    const { secretSeedTabs, onChangeSeedTab } = this.props

    if (secretSeedTabs.dataState === states.NOT_REQUESTED)
      onChangeSeedTab(SecretSeedTypes[0])
  }

  changeSeedClick(seed) {
    const { onChangeSeedTab } = this.props

    onChangeSeedTab(seed)
  }

  render() {
    const { secretSeedTabs } = this.props

    return (
      <div className={styles.secretSeedOptions}>
        {
          secretSeedTabs.dataState === states.READY
            ?
            SecretSeedTypes.map((item) => {
              return <div key={item.typeId} className={styles.secretSeedItem_container}>
                <SecretSeedTabItem
                  icon={item.icon}
                  caption={item.label}
                  active={secretSeedTabs !== null && secretSeedTabs.activeSeed.typeId === item.typeId ? true : false}
                  handleChange={this.changeSeedClick.bind(this, item)}
                />
              </div>
            })
            :
            null
        }
      </div>
    )
  }
}

export default withSecretSeedTabs(SecretSeedTabs)
