import { combineReducers } from 'redux'

import { reducer as notifReducer } from 'redux-notifications'

import activeScreenReducer from './activeScreenReducer'
import stepsReducer from './stepsReducer'
import activeStepReducer from './activeStepReducer'
import secretSeedTabsReducer from './secretSeedTabsReducer'
import requestsCounterReducer from './requestsCounterReducer'

const reducers = combineReducers({
  notif: notifReducer,

  activeScreen: activeScreenReducer,
  activeStep: activeStepReducer,
  steps: stepsReducer,
  secretSeedTabs: secretSeedTabsReducer,
  requestsCounterValue: requestsCounterReducer

  // activeScreen: activeScreenReducer,
  // secretSeedValidate: secretSeedValidateReducer,
  // secretSeedValue: secretSeedValueReducer,
  // secretSeedType: secretSeedTypeReducer,
  // stepsList: stepsListReducer,
  // stepsFinishedSteps: stepsFinishedStepsReducer
})

export default reducers