import React, { Component } from 'react';

import { withActiveScreen } from '../containers/containerActiveScreen'

import styles from './buttonScreenSwitcher.module.scss'

class ButtonScreenSwitcher extends Component {
  changeScreen() {
    const { activeScreen, onGetScreen } = this.props

    activeScreen.screen === 'start'
      ? onGetScreen('step')
      : onGetScreen('start')
  }

  render() {
    const { children } = this.props

    return (
      <div className={styles.buttonScreenSwitcher} onClick={this.changeScreen.bind(this)}>
        {children}
      </div>
    )
  }
}

export default withActiveScreen(ButtonScreenSwitcher)
