import React from 'react';

import styles from './mainButton.module.scss'

const MainButton = function (props) {
  const { type, color, label, click } = props

  return (
    <div
      className={
        color === 'primary'
          ? styles.mainButton_buttonPrimary
          : color === 'text'
            ? styles.mainButton_buttonText
            : styles.mainButton
      }
      onClick={click !== undefined ? click : null}
    >
      <div
        className={
          type === 'prev'
            ? styles.buttonInner_buttonPrev
            : type === 'next'
              ? styles.buttonInner_buttonNext
              : styles.buttonInner
        }
      >
        <span className="buttonLabel">
          {label}
        </span>
      </div>
    </div>
  )
}

export default React.memo(MainButton)
