import {
  REQUEST_CHANGE_REQUESTS_COUNT,
  RESPONSE_CHANGE_REQUESTS_COUNT,
} from '../actions/requestsCounterActions'
import { states } from './queriesStates'

const initialState = {
  dataState: states.NOT_REQUESTED,
  activeRequestValue: null
}

const requestsCounterReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_CHANGE_REQUESTS_COUNT: {
      return Object.assign(
        {},
        state,
        {
          dataState: 'STATE_REQUEST',
        }
      )
    }
    case RESPONSE_CHANGE_REQUESTS_COUNT: {
      return Object.assign(
        {},
        state,
        {
          dataState: 'STATE_READY',
          activeRequestValue: action.count
        }
      )
    }
    default:
      return state
  }
}

export default requestsCounterReducer