import { connect } from 'react-redux'
import get from 'lodash/get'

import {
  changeSeedTab as onChangeSeedTab
} from '../actions/secretSeedTabsActions'

const mapStateToProps = (state) => ({
  secretSeedTabs: {
    dataState: state.secretSeedTabs.dataState,
    activeSeed: state.secretSeedTabs.activeSeed
  }
})

const mapDispatchToProps = {
  onChangeSeedTab
}

const withSecretSeedTabs = Component => connect(mapStateToProps, mapDispatchToProps)(Component)

export { withSecretSeedTabs }