import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group'
import SecretSeedWrapper from '../steps/SecretSeedWrapper'
import NetworkOptionsWrapper from '../steps/NetworkOptionsWrapper'
import BanManagementWrapper from '../steps/BanManagementWrapper'

import { withActiveStep } from '../../containers/containerActiveStep'
import { states } from '../../reducers/queriesStates'

import styles from './stepScreenRight.module.scss'
import fadeTransition from "../../transitions/fade.module.scss"

class StepScreenRight extends Component {
  render() {
    const { activeStep } = this.props

    return (
      <div className={styles.stepScreenRight}>
        {
          activeStep.dataState === states.READY
            ?
            <div className={styles.stepContent_container}>
              <CSSTransition
                timeout={500}
                in={activeStep.step.name === 'secretSeed'}
                classNames={fadeTransition}
                unmountOnExit
              >
                <SecretSeedWrapper />
              </CSSTransition>

              <CSSTransition
                timeout={500}
                in={activeStep.step.name === 'networkOptions'}
                classNames={fadeTransition}
                unmountOnExit
              >
                <NetworkOptionsWrapper />
              </CSSTransition>

              <CSSTransition
                timeout={500}
                in={activeStep.step.name === 'banManagement'}
                classNames={fadeTransition}
                unmountOnExit
              >
                <BanManagementWrapper />
              </CSSTransition>
              {/* <SecretSeedWrapper /> */}
              {/* <NetworkOptionsWrapper /> */}
              {/* <BanManagementWrapper /> */}
            </div>
            :
            null
        }

      </div>
    )
  }
}

export default withActiveStep(StepScreenRight);
