import { connect } from 'react-redux'
import get from 'lodash/get'

import {
  getScreen as onGetScreen
} from '../actions/activeScreenActions'

const mapStateToProps = (state) => ({
  activeScreen: {
    dataState: state.activeScreen.dataState,
    screen: state.activeScreen.screen
  }
})

const mapDispatchToProps = {
  onGetScreen
}

const withActiveScreen = Component => connect(mapStateToProps, mapDispatchToProps)(Component)

export { withActiveScreen }