import React, { Component } from 'react';
import NavStep from './NavStep'

import { withActiveStep } from '../../containers/containerActiveStep'
import { withSteps } from '../../containers/containerSteps'
import { states } from '../../reducers/queriesStates'

import styles from './mainNav.module.scss'

class MainNav extends Component {
  componentDidMount() {
    const { onGetStepsList, steps, onSetStep, activeStep } = this.props

    if (steps.dataState === states.NOT_REQUESTED)
      onGetStepsList()

    if (steps.dataState === states.READY
      && activeStep.dataState === states.NOT_REQUESTED)
      onSetStep(steps.stepsList[0])
  }

  componentDidUpdate(prevProps) {
    const { onSetStep, steps, activeStep } = this.props

    if (steps.dataState === states.READY
      && activeStep.dataState === states.NOT_REQUESTED
      && prevProps.activeStep.dataState !== states.READY) {
      onSetStep(steps.stepsList[0])
    }

  }

  render() {
    const { steps, activeStep } = this.props

    return (
      <ul className={styles.mainNav}>
        {
          steps.dataState === states.READY && activeStep.step !== null
            ? steps.stepsList.map((item) => {
              return <NavStep key={item.id} label={item.label} id={item.id} />
            })
            : null
        }
      </ul>
    )
  }
}

export default withSteps(withActiveStep(MainNav));
