const REQUEST_CHANGE_REQUESTS_COUNT = 'REQUEST_CHANGE_REQUESTS_COUNT'
const RESPONSE_CHANGE_REQUESTS_COUNT = 'RESPONSE_CHANGE_REQUESTS_COUNT'

const requestChangeRequestsCountAction = () => {
  return {
    type: REQUEST_CHANGE_REQUESTS_COUNT
  }
}

const responseChangeRequestsCountAction = (count) => {
  return {
    type: RESPONSE_CHANGE_REQUESTS_COUNT,
    count
  }
}

const changeRequestsCount = (count) => {
  return (dispatch) => {
    dispatch(requestChangeRequestsCountAction());
    dispatch(responseChangeRequestsCountAction(count))
  }
}

export {
  requestChangeRequestsCountAction,
  responseChangeRequestsCountAction,
  changeRequestsCount,
  REQUEST_CHANGE_REQUESTS_COUNT,
  RESPONSE_CHANGE_REQUESTS_COUNT,
}
