import React from 'react';
import Popover from '@material-ui/core/Popover';
import { ThemeProvider } from "@material-ui/styles";
import CustomInput from './Input';
import ButtonPopover from './ButtonPopover';
import MainIcon from './MainIcon';

import { muiCustomTheme } from './static.MuiCustomTheme'

import styles from './popoverInputWrapper.module.scss'

const PopoverInputWrapper = function (props) {
  const { placeholder, id, value } = props
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleOpenPopover = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClosePopover = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const popoverId = open ? 'simple-popover' : undefined;

  return (
    <div className={styles.popoverInputWrapper}>
      <CustomInput
        id={id}
        placeholder={placeholder}
        value={value !== undefined ? value : ''}
      />

      <div className={styles.buttonPopover_container} aria-describedby={popoverId} onClick={handleOpenPopover}>
        <ButtonPopover />
      </div>

      <ThemeProvider theme={muiCustomTheme}>
        <Popover
          id={popoverId}
          open={open}
          anchorEl={anchorEl}
          onClose={handleClosePopover}
          anchorOrigin={{
            vertical: -10,
            horizontal: 36,
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
        >
          {props.children}
          <span className={styles.btnClosePopover_container} onClick={handleClosePopover}>
            <MainIcon name='close' />
          </span>
        </Popover>
      </ThemeProvider>
    </div >
  )
}

export default React.memo(PopoverInputWrapper)
