import React, { Component } from 'react';

import { withRequestsCounter } from '../../../containers/containerRequestsCounter';
import RequestsCounterNums from '../../static/static.requestsCounter'
import { states } from '../../../reducers/queriesStates'

import styles from './requestsCounter.module.scss'

class RequestsCounter extends Component {
  componentDidMount() {
    const { requestsCounterValue, onChangeRequestsCount } = this.props

    if (requestsCounterValue.dataState === states.NOT_REQUESTED)
      onChangeRequestsCount(RequestsCounterNums[1])
  }

  changeCounterClick(count) {
    const { onChangeRequestsCount } = this.props

    onChangeRequestsCount(count)
  }

  render() {
    const { requestsCounterValue } = this.props

    return (
      <div className={styles.requestsCounterWrapper}>
        {
          requestsCounterValue.dataState === states.READY
            ?
            RequestsCounterNums.map((item) => {
              return <div key={item.id} className={styles.requestsCounterItem_container}>
                <span
                  className={
                    requestsCounterValue !== null && requestsCounterValue.activeRequestValue.id === item.id
                      ? styles.requestsCounterItem_active
                      : styles.requestsCounterItem
                  }
                  onClick={this.changeCounterClick.bind(this, item)}
                >
                  {item.label}
                </span>
              </div>
            })
            :
            null
        }
      </div>
    )
  }
}

export default withRequestsCounter(RequestsCounter)
