import {
  REQUEST_OPEN_SCREEN,
  RESPONSE_OPEN_SCREEN,
} from '../actions/activeScreenActions'

import { states } from './queriesStates'

const initialState = {
  dataState: states.NOT_REQUESTED,
  screen: null
}

const activeScreenReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_OPEN_SCREEN: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.REQUEST
        }
      )
    }
    case RESPONSE_OPEN_SCREEN: {
      return Object.assign(
        {},
        state,
        {
          dataState: states.READY,
          screen: action.screen
        }
      )
    }
    default:
      return state
  }
}

export default activeScreenReducer