import React from 'react';
import { ThemeProvider } from "@material-ui/styles"
import Title from '../../shared/Title'
import RequestsCounter from '../steps/controls/RequestsCounter'
import BanDurationValue from '../steps/controls/BanDurationValue'
import StepsControls from '../../shared/StepsControls'
import FormLabel from '@material-ui/core/FormLabel'

import { muiCustomTheme } from '../../shared/static.MuiCustomTheme'

import styles from './stepContent.module.scss'

const BanManagementWrapper = function () {
    return (
        <div className={styles.stepContent}>
            <div className={styles.secondTitle_container}>
                <Title type="secondary" title="Ban Management" />
            </div>

            <div className={styles.stepContentItem_container}>
                <div className={styles.label_container}>
                    <ThemeProvider theme={muiCustomTheme}>
                        <FormLabel focused>Max Failed Requests intil an address is banned</FormLabel>
                    </ThemeProvider>

                </div>

                <RequestsCounter />
            </div>

            <div className={styles.stepContentItem_container}>
                <div className={styles.label_container}>
                    <ThemeProvider theme={muiCustomTheme}>
                        <FormLabel focused>Ban Duration, ms</FormLabel>
                    </ThemeProvider>
                </div>

                <BanDurationValue />
            </div>

            <div className={styles.stepsControls_container}>
                <StepsControls />
            </div>
        </div>
    )
}

export default React.memo(BanManagementWrapper)