import React, { Component } from 'react';
import { ThemeProvider } from '@material-ui/styles'
import FormControl from '@material-ui/core/FormControl'
import TextField from '@material-ui/core/TextField'
import { Autocomplete } from '@material-ui/lab'
import Chip from '@material-ui/core/Chip'
import TokenInput from 'react-customize-token-input';
import { withStyles } from '@material-ui/core/styles'
import MainIcon from './MainIcon';

import { muiCustomTheme } from './static.MuiCustomTheme'
// import { styles } from './static.multiInput'

import styles from './multiInput.module.scss'

class CustomMultiInput extends Component {
  render() {
    const { placeholder, id, value, classes } = this.props

    return (
      // <ThemeProvider theme={muiCustomTheme}>
      //   <FormControl>
      //     <TextField
      //       id={id}
      //       label={placeholder}
      //       value={value !== undefined ? value : null}
      //     />
      //   </FormControl>
      // </ThemeProvider>
      <ThemeProvider theme={muiCustomTheme}>
        <Autocomplete
          className={classes.root}
          multiple
          id={id}
          options={[]}
          freeSolo
          fullWidth
          renderTags={(value, getTagProps) =>
            value.map((option, index) => (
              <Chip variant="outlined" label={option} deleteIcon={<div><MainIcon name="close" /></div>} {...getTagProps({ index })} />
            ))
          }
          disableClearable
          autoSelect
          renderInput={(params) => (
            <FormControl>
              <TextField {...params} label={placeholder} />
            </FormControl>
          )}
          onChange={(event, value) => {
            console.log(event, value)
          }}
        />
      </ThemeProvider>
    )
  }
}

export default withStyles(styles)(CustomMultiInput)
