import React from 'react';
import Title from '../../shared/Title'
import SecretSeedTabs from '../steps/controls/SecretSeedTabs'
import SecretSeedValue from '../steps/controls/SecretSeedValue'
import StepsControls from '../../shared/StepsControls'

import styles from './stepContent.module.scss'

const SecretSeedWrapper = function () {
    return (
        <div className={styles.stepContent}>
            <div className={styles.secondTitle_container}>
                <Title type="secondary" title="Secret Seed" />
            </div>

            <div className={styles.stepContentItem_container}>
                <SecretSeedTabs />
            </div>

            <div className={styles.stepContentItem_container}>
                <SecretSeedValue />
            </div>

            <div className={styles.stepsControls_container}>
                <StepsControls />
            </div>
        </div>
    )
}

export default React.memo(SecretSeedWrapper)