import navService from '../shared/services/navService'

const REQUEST_STEPS_LIST = 'REQUEST_STEPS_LIST'
const RESPONSE_STEPS_LIST = 'RESPONSE_STEPS_LIST'

const requestStepsList = () => {
  return {
    type: REQUEST_STEPS_LIST
  }
}

const responseStepsList = (stepsList) => {
  return {
    type: RESPONSE_STEPS_LIST,
    stepsList
  }
}

const getStepsList = () => {
  return (dispatch) => {
    dispatch(requestStepsList())
    dispatch(responseStepsList(navService.getNavList()))
  }
}

export {
  requestStepsList,
  responseStepsList,
  getStepsList,
  REQUEST_STEPS_LIST,
  RESPONSE_STEPS_LIST,
}
