import React from 'react';

import { withActiveStep } from '../../containers/containerActiveStep'

import styles from './navStep.module.scss'

const NavStep = function (props) {
  const { label, id, activeStep } = props

  return (
    <li
      className={
        id === activeStep.step.id
          ? styles.navStep_active
          : styles.navStep
      }
    >
      {label}
    </li>
  )
}

export default React.memo(withActiveStep(NavStep));
