import { connect } from 'react-redux'

import {
  setStep as onSetStep,
  resetActiveStep as onResetActiveStep
} from '../actions/activeStepActions'

const mapStateToProps = (state) => ({
  activeStep: {
    dataState: state.activeStep.dataState,
    step: state.activeStep.step
  }
})

const mapDispatchToProps = {
  onSetStep,
  onResetActiveStep
}

const withActiveStep = Component => connect(mapStateToProps, mapDispatchToProps)(Component)

export { withActiveStep }